export function breadcrumbInit() {
    var scrollTimeout;
    var scrolled = false; // Флаг для отслеживания факта прокрутки

    $('.breadcrumb')
        .css('justify-content', 'unset') // Максимальный скролл крошек
        .scrollLeft($('.breadcrumb')[0].scrollWidth);

    $('.breadcrumb').on('scroll', function() {
        var $this = $(this);

        // Добавляем класс, когда элемент прокручивается
        $this.addClass('scrolling');

        // Если элемент уже был прокручен, то устанавливаем таймаут для удаления класса
        if (scrolled) {
            clearTimeout(scrollTimeout);
            scrollTimeout = setTimeout(function() {
                $this.removeClass('scrolling');
            }, 200);
        }

        // Устанавливаем флаг, что элемент был прокручен
        scrolled = true;
    });
}
