import { toast } from '@/js/components/toast';
export function cityChooseInit() {
    const citiesWrapper = $('.city-change__cities');
    const citySuggestModal = new bootstrap.Modal($('#citySuggestModal'), {});
    const cityChangeModal = new bootstrap.Modal($('#cityChangeModal'), {});
    const citySelectedModal = new bootstrap.Modal($('#citySelectedModal'), {});

    /* --- ЛОГИКА ПОПАПА --- */
    // citySuggestModal.show()

    // Изменение города (показ модалки)
    $('.js-change-location').on("click", () => cityChangeModal.show());

    /* --- ЛОГИКА ИЗМЕНЕНИЯ ГОРОДА --- */

    const cities = [
        {
            "id": 0,
            "name": "Москва и область",
            "region": "",
            "coords": "55.755826, 37.6172999",
            "selectable": true,
        },
        {
            "id": 1,
            "name": "Санкт-Петербург и область",
            "region": "",
            "coords": "59.9342802, 30.3350986",
            "selectable": true,
        },
        {
            "id": 2,
            "name": "Воронежская область",
            "region": "",
            "coords": "51.6754966, 39.2088823",
            "selectable": true,
        },
        {
            "id": 3,
            "name": "Смоленская область",
            "region": "",
            "coords": "54.782635, 32.045251",
            "selectable": true,
        },
        {
            "id": 4,
            "name": "Астрахань",
            "region": "Астраханская область",
            "coords": "46.347869, 48.033574",
            "selectable": true,
        },
        {
            "id": 5,
            "name": "Пермь",
            "region": "Пермский край",
            "coords": "58.0104501, 56.2294347",
            "selectable": true,
        },
        {
            "id": 6,
            "name": "Белгород",
            "region": "Белгородская область",
            "coords": "50.59566, 36.587223",
            "selectable": true,
        },
        {
            "id": 7,
            "name": "Липецк",
            "region": "Липецкая область и тут еще чуть-чуть добавил текста",
            "coords": "52.6122006, 39.5981225",
            "selectable": true,
        },
        {
            "id": 8,
            "name": "Село Королевка",
            "region": "Новосибирская область",
            "coords": "49.922223, 42.517030",
            "selectable": false,
        },
        {
            "id": 9,
            "name": "Село Ташара",
            "region": "Новосибирская область",
            "coords": "49.251411, 31.622722",
            "selectable": false,
        },
        {
            "id": 10,
            "name": "Село Мельниково",
            "region": "Томская область, ооочень длинный текст чтобы посмотреть как переносится",
            "coords": "54.829651, 53.743666",
            "selectable": false,
        },
        {
            "id": 11,
            "name": "Село Егорово",
            "region": "Новосибирская область",
            "coords": "53.8902528, 27.5677184",
            "selectable": false,
        },
        {
            "id": 12,
            "name": "Село Сельское",
            "region": "Московская область",
            "coords": "48.304524, 53.732607",
            "selectable": false,
        },
        {
            "id": 13,
            "name": "Село Селянское",
            "region": "Московская область",
            "coords": "54.537803, 43.307361",
            "selectable": false,
        },
        {
            "id": 14,
            "name": "Село Королевка",
            "region": "Томская область",
            "coords": "49.922223, 42.517030",
            "selectable": false,
        },
    ];

    // Рендер города
    const renderCity = (city) => {
        const cityLayout = `
            <div class="city-change__city js-city" data-id="${city.id}">
                <div class="city-change__city-text">${city.name}</div>
                ${city.region ? `<div class="city-change__city-text city-change__city-region">${city.region}</div>` : ''}
            </div>
        `;
        citiesWrapper.append(cityLayout)
    };
    // Сначала отображаем 30 городов (выполняется при загрузке страницы)
    $(document).ready(() => restoreCities());

    // Функция подсчета расстояния между двумя точками с учетом радиуса земли
    function calculateDistance(lat1, lon1, lat2, lon2) {
        const R = 6371; // Радиус Земли в км
        const dLat = (lat2 - lat1) * Math.PI / 180;
        const dLon = (lon2 - lon1) * Math.PI / 180;
        const a =
            0.5 - Math.cos(dLat) / 2 +
            Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
            (1 - Math.cos(dLon)) / 2;

        return R * 2 * Math.asin(Math.sqrt(a));
    }

    // Рендер города который нельзя выбрать (ДЛЯ ПОИСКА)
    const renderFoundUnselectable = (city) => {
        // Преобразование координат выбранного города из строки в числа
        const [cityLat, cityLon] = city.coords.split(', ').map(Number);

        // Фильтрация городов, которые можно выбрать, и расчет расстояния от выбранного города
        const nearestCities = cities
            .filter(c => c.selectable === true && c.id !== city.id) // Исключаем текущий город
            .map(c => {
                const [lat, lon] = c.coords.split(', ').map(Number);
                const distance = calculateDistance(cityLat, cityLon, lat, lon);
                return {...c, distance};
            })
            // Сортировка городов по расстоянию
            .sort((a, b) => a.distance - b.distance)
            // Выбор трех самых ближайших городов
            .slice(0, 3);

        // Разметка ближайших городов с аптеками
        const citiesLayout = nearestCities.map(city => `
            <div class="city-change__cities-f-nearest-city city-change__city js-city" data-id="${city.id}">
                <div class="city-change__city-text">${city.name}</div>
                <div class="city-change__cities-f-nearest-city-bottom">
                    <div class="city-change__city-text city-change__city-region">${city.region}</div>
                    <div class="city-change__city-text city-change__city-distance">${city.distance.toFixed(1)} км</div>
                </div>
            </div>
        `).join('');

        // Основная разметка
        const layout = `
            <div class="city-change__cities-f city-change__cities-f--nearest">
                <div class="city-change__cities-f-city city-change__city city-change__city--disabled">
                    <div class="city-change__city-text">${city.name}</div>
                    <div class="city-change__city-text city-change__city-region">${city.region}</div>
                </div>
                <div class="city-change__cities-f-info">
                    К сожалению, этот город невозможно выбрать, но вы можете продолжить<br>
                    поиск в ближайших аптеках, выбрав регион
                    <span class="city-change__cities-f-suggest js-select-region" data-region="${city.region}">${city.region}</span>
                </div>
                <div class="city-change__cities-f-nearest">
                    <div class="city-change__cities-f-nearest-info city-change__cities-f-info">Ближайшие аптеки</div>
                    <div class="city-change__cities-f-nearest-cities">${citiesLayout}</div>
                </div>
            </div>
        `;

        citiesWrapper.append(layout);
    };

    // Рендер города который нельзя выбрать (ДЛЯ ГЕОЛОКАЦИИ)
    const renderGeoFoundUnselectable = (city) => {
        // Основная разметка
        const layout = `
            <div class="city-change__cities-f city-change__cities-f--nearest">
                <div class="city-change__cities-f-city city-change__city city-change__city--disabled">
                    <div class="city-change__city-text">${city.name}</div>
                    <div class="city-change__city-text city-change__city-region">${city.region}</div>
                </div>
                <div class="city-change__cities-f-info">
                    К сожалению, этот город невозможно выбрать, но вы можете продолжить<br>
                    поиск в ближайших аптеках, выбрав регион
                    <span class="city-change__cities-f-suggest js-select-region" data-region="${city.region}">${city.region}</span>
                </div>
                <div class="city-change__cities-f-buttons">
                    <button class="button button-primary js-select-region" data-region="${city.region}" type="button">
                        Выбрать весь регион
                    </button>
                    <button class="button button-solid js-reselect" type="button">
                        Нет, выбрать в ручную
                    </button>
                </div>
            </div>
        `;

        citiesWrapper.append(layout);
    };

    // Отобразить город который можно выбрать (ДЛЯ ГЕОЛОКАЦИИ)
    const renderFoundSelectable = (city) => {
        const layout = `
            <div class="city-change__cities-f">
                <div class="city-change__cities-f-city city-change__city city-change__city--enabled">
                    <div class="city-change__city-text">${city.name}</div>
                    <div class="city-change__city-text city-change__city-region">${city.region}</div>
                </div>
                <div class="city-change__cities-f-buttons">
                    <button class="button button-primary js-city" data-id="${city.id}" type="button">
                        Да
                    </button>
                    <button class="button button-solid js-reselect" type="button">
                        Нет, выбрать в ручную
                    </button>
                </div>
            </div>
        `;
        // Добавить новый layout
        citiesWrapper.append(layout);
    };

    // Отобразить Not Found
    const renderNotFound = (query) => {
        const layout = `
            <div class="city-change__cities-nf">
                <div class="city-change__cities-nf-title">
                    По запросу «<span class="city-change__cities-nf-query">${query}</span>» ничего не найдено
                </div>
                <div class="city-change__cities-nf-info">
                    Попробуйте выбрать ваш регион для отображения полного<br>
                    количества аптек, например <span class="city-change__cities-nf-suggest js-select-region" data-region="Московская область">Московская область</span>
                </div>
            </div>
        `;

        // Добавить новый layout
        citiesWrapper.append(layout);
    };

    // Фильтрация (поиск городов)
    const filterCities = () => {
        const value = $(".js-city-search-input").val().toLowerCase();
        const valueUnformatted = $(".js-city-search-input").val();
        const $loading = $('.city-change__input-loading');
        const $clearBtn = $('.city-change__input-clear');

        restoreCities();
        if (value === '') {
            $clearBtn.fadeOut(100);
            return;
        } else if (value === 'show1') {
            $clearBtn.fadeIn(100);
            // Показ города, который нельзя выбрать
            var nonSelectableCities = cities.filter(city => {
                return !city.selectable;
            })[0]
            citiesWrapper.empty();
            renderGeoFoundUnselectable(nonSelectableCities);
            return;
        } else if (value === 'show2') {
            $clearBtn.fadeIn(100);
            var selectableCities = cities.filter(city => {
                return city.selectable;
            })[4];
            // Показ города, который можно выбрать
            citiesWrapper.empty();
            renderFoundSelectable(selectableCities);
            return;
        } else if (value === 'show3') {
            $clearBtn.fadeIn(100);

            toast.showToastWarning('Не удалось определить ваше местоположение')

            return;
        }

        $clearBtn.fadeOut(100);
        $loading.fadeIn(100);

        // Очистка контейнера городов перед новым рендерингом
        citiesWrapper.empty();

        // Фильтрация городов
        const filteredCities = cities.filter(city =>
            city.name.toLowerCase().includes(value)
        );


        // Рендеринг отфильтрованных городов (максимум 30)
        if (filteredCities.length > 0) {
            filteredCities.slice(0, 30).forEach(city => { // Ограничение массива до первых 30 элементов
                if (city.selectable === false) {
                    renderFoundUnselectable(city);
                } else {
                    renderCity(city);
                }
            });
        } else {
            renderNotFound(valueUnformatted);
        }

        setTimeout(() => {
            $loading.fadeOut(100);
            value !== '' ? $clearBtn.fadeIn(100) : $clearBtn.fadeOut(100);
        }, 500);
    };

    // Определение локации пользователя
    const defineLocation = () => {
        const $loading = $('.city-change__define-loading')
        const $icon = $('.city-change__define-icon')
        $icon.css("opacity", "0");
        $loading.fadeIn(100);

        if ('geolocation' in navigator) {
            navigator.geolocation.getCurrentPosition(position => {
                const latitude = position.coords.latitude;
                const longitude = position.coords.longitude;

                console.log(`Latitude: ${latitude}, Longitude: ${longitude}`);

                const closestCity = (() => {
                    const distanceToCity = (lat1, lon1, lat2, lon2) => calculateDistance(lat1, lon1, lat2, lon2);

                    let minDistance = Infinity;
                    let closestCityId = null;

                    for (const city of cities) {
                        if (city.coords) {
                            const [cityLat, cityLon] = city.coords.split(", ").map(Number);
                            const distance = distanceToCity(latitude, longitude, cityLat, cityLon);

                            if (distance < minDistance) {
                                minDistance = distance;
                                closestCityId = city.id;
                            }
                        }
                    }

                    return cities[closestCityId];
                })();

                if (!closestCity.selectable) {
                    // Показ города, который нельзя выбрать
                    citiesWrapper.empty();
                    renderGeoFoundUnselectable(closestCity);
                } else {
                    citiesWrapper.empty();
                    renderFoundSelectable(closestCity);
                }

            }, error => {
                console.error("Error Code = " + error.code + " - " + error.message);

                toast.showToastWarning('Не удалось определить ваше местоположение')
            });
        } else {
            console.log("Geolocation is not supported by this browser.");
        }

        setTimeout(() => {
            $loading.fadeOut(100);
            $icon.css("opacity", "1");
        }, 500);
    }
    const restoreCities = () => {
        const $clearBtn = $('.city-change__input-clear');
        $clearBtn.fadeOut(100);
        citiesWrapper.empty();

        // Вывод первых 30 городов, которые можно выбрать
        cities.filter(city => city.selectable === true).slice(0, 30).forEach(city => renderCity(city));
    };

    // Выбрать город
    const selectCity = (city) => {
        const $loading = $('.city-change__choosen-loading');
        const $cityChoosen = $('.js-city-choosen-city');
        const $regionChoosen = $('.js-city-choosen-region');
        const $input = $(".city-change__input");

        $loading.fadeIn(100);

        // Обработка нажатия на город / область, которые можно выбрать
        $input.val('');
        restoreCities();
        $cityChoosen.text(city.name);
        $regionChoosen.text(city.region);

        setTimeout(() => {
            $loading.fadeOut(100);
            cityChangeModal.hide();
            citySuggestModal.hide();
            citySelectedModal.show();

            setTimeout(() => {
                citySelectedModal.hide();
            }, 4000);

        }, 500);
    };

    // Обработка клика на определение локации
    $(".city-change__define").on("click", defineLocation);

    $(".city-change__input-clear").on("click", () => {
        const $input = $(".city-change__input");
        $input.val('');
        filterCities();
    })

    // Обработка ввода значения в input поиска с debounce
    let debounceTimer;
    $(".city-change__input").on("input", () => {
        clearTimeout(debounceTimer);
        debounceTimer = setTimeout(() => {
            filterCities();
        }, 500); // Задержка в 500 мс
    });

    // Listener на клик по городу (выбор города)
    citiesWrapper.on("click", ".js-city", function (event) {
        const id = $(this).data('id');
        const city = cities.find(c => c.id === id);

        if (!$(this).hasClass('city-change__city--disabled') && !$(this).hasClass('city-change__city--enabled')) {
            if (!city.selectable) {
                // Обработка нажатия на город, который нельзя выбрать
                citiesWrapper.empty();
                renderFoundUnselectable(city);
            } else {
                // Обработка нажатия на город, который можно выбрать
                selectCity(city);
            }
        }
    });

    // Обработка клика на выбор региона
    citiesWrapper.on("click", ".js-select-region", function (e) {
        const $loading = $('.city-change__choosen-loading');
        $loading.fadeIn(100);

        selectCity({"name": `${$(e.target).data('region')}`, "region": ""});

        setTimeout(() => {
            $loading.fadeOut(100);
        }, 500);
    });

    // Обработка клика на перевыбор города
    citiesWrapper.on("click", ".js-reselect", function () {
        const $loading = $('.city-change__choosen-loading');
        $loading.fadeIn(100);

        const $input = $(".city-change__input");
        $input.val('');
        filterCities();

        setTimeout(() => {
            $loading.fadeOut(100);
        }, 500);
    });
}