const ScrollToSects = anchorScroll();

new ScrollToSects({
    linksContainer: '.instructions__anchors, .product-nav__list',//контейнер, в котором лежат кнопки навигации. Если контейнеров несколько, перечислить ч/з запятую.
    // offset: -50,//отступ от верха экрана при прокрутке (если нужен)
    sectsSelector: '[data-anchor-target]',//селектор секций, default - "section"
     // delay: 300,//задержка перед прокруткой. Может понадобится, елсли перед прокруткой нужно время на анимацию закрытия моб. меню, например
     // anchorSpy: false, //добавление активного класса ссылке при скролле, если соответствующая ей секция попадает в экран
     activeClassAdding: true, //добавление классов активным ссылкам
    // afterNavClick: function(){
    //   // выполнится после нажатия на любою кнопку навигации, передзадержкой, если она задана

    // }
  });

$(window).on('scroll', mapCheckScroll);

const header = $(".header__top");
const headerFixed = $(".header__fixed, .scroll-up");
const headerHeight = header.innerHeight();
const body = $("body");

const heightHeader = () => {
	body.css("--hh", `${Math.round(headerHeight)}px`);
};

heightHeader();

const fixedHeader = () => {
	const scroll = $(window).scrollTop();
	if (scroll >= headerHeight) {
		headerFixed.addClass("is-active");
	} else {
		headerFixed.removeClass("is-active");
	}
};

fixedHeader();


// Дополнительно: скрытие tooltip при скролле
$(window).on('scroll', function () {
	$('.tooltip').tooltip('hide');
});
// Глобальный обработчик клика для скрытия тултипов при клике за их пределами

const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
const tooltipArray = Array.from(tooltipTriggerList); // Преобразуем в массив

const tooltipList = tooltipArray.map((tooltipTriggerEl) => {
	let tooltip = new bootstrap.Tooltip(tooltipTriggerEl, {
		offset: [0, 10],
	});

	// Добавляем обработчик клика на сам элемент
	tooltipTriggerEl.addEventListener('click', function(event) {
		event.preventDefault(); // Предотвращаем стандартное действие
		this.focus(); // Устанавливаем фокус на элемент
	});

	return tooltip;
});

$(window).on("resize", heightHeader);

$(window).scroll(fixedHeader);
