import { toast } from '@/js/components/toast';

export const switcherToasts = () => {
	$('[data-toast-switcher]').on('change', (e) => {
		console.log(e.target);
		if(e.target.checked){
			toast.showToastSubscribeAdd('Уведомим на почту ivanov@mail.ru,<br> как только товар появится в продаже');
		}else{
			toast.showToastSubscribeRemove('Вы отписались<br> от уведомления о товаре');
		}
	})
}